@keyframes showCover {
	0% {
	  opacity: 0.5;
	}
	50% {
	  opacity: 0.75;
	}
	100% {
	  opacity: 1;
	}
  }
  
  @keyframes slideX {
	0% {
	  transform: translateX(-50%);
	}
	50% {
	 transform: translateX(100%);
	}
	100% {
	  transform: translateX(0%);
	}
  }
  
  @keyframes slideY {
	0% {
	  transform: translateY(-100%);
	  opacity: 0.5;
	  font-size: 0.5vmin;
	}
	50% {
	 transform: translateY(100%);
	 opacity: 0.75;
	 font-size: 1vmin;
	}
	100% {
	  transform: translateY(0%);
	  opacity: 1;
	}
  }

  @keyframes growTitle {
	from {
	  font-size: 10px;
	}
	to {
	  font-size: 4vmin;
	}
  }

  @keyframes growWord {
	from {
	  font-size: 5px;
	}
	to {
	  font-size: 2.5vmin;
	}
  }

  @keyframes grow {
	from {
	  height: 5px;
	  width: 5px;
	  opacity: 0;
	}
	to {
	  height: 4vmin;
	  width: 4vmin;
	  opacity: 1;
	}
  }

  @keyframes growArrow {
	from {
	  height: 5px;
	  width: 5px;
	  opacity: 0;
	}
	to {
	  height: 4vmin;
	  width: 4vmin;
	  opacity: 0.8;
	}
  }

  @keyframes expandTo {
	from {
	  width: 0;
	}
	to {
	  width: 30vmin
	}
  }
  
  @keyframes expandToD {
	from {
	  width: 0;
	}
	to {
	  width: 22vmin;
	}
  }

  @keyframes expandErrandButtons {
	from {
		width: 0;
		font-size: 0;
	}
	to {
		width: 30%;
		font-size: 1vw;
	}
  }
