@import url("./home.animations.css");
@import url("../home/App.links.styles.css");

#cover {
  --primary-color: rgb(0, 120, 240);
  --secondary-color: rgb(97, 194, 162);
  --tertiary-color: rgb(14, 174, 232);

  --name-color: rgb(125, 176, 140);
  --first-name-shadow-color: rgb(15, 48, 60);
  --last-name-shadow-color: rgb(11, 35, 43);

  --full-height: 100vh;
  --full-width: 100vw;
  --profile-pic-diameter: 25vw;

  --center-height: 30vh;
  --center-width: 100vw;
  /* 
  resolution 1600 x 900
  13vw = 208
  70vh = 630
  */

  display: flex;
  flex-direction: column;
  margin: 0;
  height: var(--full-height);
  width: var(--full-width);
  box-sizing: border-box;
  /* background: radial-gradient(
    #1a586e 36%, 
    #103746 45%, 
    #0d2731 56%, 
    #07161b 62%, 
    #050f13 81%
  ); */
}

#cover:fullscreen {
  height: 100vh;
}

#skills-butt {
  position: absolute;
  top: 30em;
}

/* RESUME */
#resume-modal-close-button {
  font-size: 1.8em;
  color: rgb(179, 0, 0);
  font-weight: bold;
  position: absolute;
  left: 87vw;
  top: 2.4vh;
  z-index: 9;
}

#resume-modal-close-button:hover {
  color: rgb(255, 0, 0);
  /* color: rgb(0, 69, 138); */
  /* color: rgb(98, 134, 243); */
  /* color: rgb(11, 8, 100); */
  /* color: rgb(0, 69, 138); */
  cursor: pointer;
}

#resume-modal {
  position: absolute;
  z-index: 9;
  width: 100vw;
  height: 100vh;
  margin: auto;
  padding: 0 2rem;
}

/* CENTER */
#center {
  display: flex;
  position: absolute;
  z-index: 8;
}

#peter {
  transform: rotate(12.1deg) skewX(-5deg);
}

#czerniak {
  transform: rotate(-6.1deg) skewX(-5deg);
}

#peter, #czerniak {
  text-transform: uppercase;
  font-weight: lighter;
  font-size: 2.3em;
  letter-spacing: 5px;
  font-family: "Verdana";
}

#peter:hover, #czerniak:hover {
  cursor: pointer;
}

.polygon {
  /* color: rgb(255, 255, 255); */
  width: calc(var(--profile-pic-diameter) * 0.5);
  mask: url(#transparent-text);
}

.pc {
  background: radial-gradient(
    rgba(147, 233, 190, 0.3) 30%, 
    rgba(34, 167, 101, 0.9) 100%);
  border-radius: 100%;
  box-shadow: -6px -6px 27px var(--secondary-color), 6px 6px 27px var(--secondary-color);
  /* border-top: 1px solid var(--primary-color);
  border-bottom: 1px solid var(--primary-color);
  border-left: 0.6em solid var(--primary-color);
  border-right: 0.6em solid var(--primary-color); */
  position: absolute;
  z-index: 5;
}

/* MENU */
.menubutton {
  text-transform: uppercase;
  font-family: "Courier New";
  transform: skewY(1px);
  font-weight: bold;
}

.menubutton:hover {
  cursor: pointer;
}

.link-item {
  /* width: 3.5em; */
  /* height: 2em; */
  /* padding: 2.5vmin; */
}

.link-item:hover {
  cursor: pointer;
}

/* CONTENT */
.content {
  display: flex;
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  /* margin: 1em 0; */
}

.content::-webkit-scrollbar {
  display: none;
}

#fuel-link {
  font-family: var(--fuel-title-font-family);
}

#budget-link {
  font-family: var(--budget-title-font-family);
}

#errands-link {
  font-family: var(--errands-title-font-family);
}

#watch-your-step-link {
  font-family: var(--watch-your-step-link);
}

#portal-link {
  font-family: var(--portal-font-family);
}

#intro-content, 
#about-content, 
#projects-content {
  line-height: 0.2em;
}

#about-content {
  display: flex;
  align-items: center;
  color: blue;
  justify-content: center;
}

.content-body:hover {
  cursor: pointer;
}

/* .project-link:visited {
  color: white;
} */

.contact-item {
  padding: 0.3em;
  width: 2.7em;
  height: 1em;
}

#contact-content {
  padding-top: 10px;
}

#content-description {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  text-align: center;
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

#content-description::-webkit-scrollbar {
  display: none;
  /* color: rgba(17, 145, 56, 0.9); */
}

.marker {
  /* color: rgb(255, 255, 255); */
}

.x {
  fill: rgb(255, 0, 255);
}

.char {
  fill: rgb(99, 0, 0);
}