@keyframes dissolve {
	from {
	  opacity: 0;
	}
	to {
	  opacity: 1;
	}
}

@keyframes slideFromLeft {
	from {
	  transform: translateX(-1000%);
	  font-size: 0;
	  opacity: 0;
	}
	to {
	  transform: translateX(0%);
	  opacity: 1;
	  font-size: 5vmin;
	}
  }
  
@keyframes slideFromRight {
	from {
		transform: translateX(200%);
	}
	to {
		transform: translateX(0%);
	}
}

@keyframes zoomIn {
	from {
		width: 0;
		height: 0;
	}
	to {
		width: 30vmin;
		height: 30vmin;
	}
}

@keyframes plopMenuButton1 {
	from {
	  transform: translateY(-1000%);
	  width: 0;
	  height: 0;
	  font-size: 0;
	  box-shadow: unset;
	}
	to {
	  width: 7.5vmin;
	  height: 7.5vmin;
	  font-size: 2.5vmin;
	  box-shadow: 2px 2px 99px #61C2A2;
	  transform: translateY(0%)
	}
  }
  
