@keyframes slideFromBottom {
	from {
	  transform: translateY(1200%);
	}
	to {
	  transform: translateY(0%);
	}
}

@keyframes revealString {
	0% {
	  opacity: 0;
	  color: rgba(204, 224, 210, 0.1);
	}
	30% {
	  opacity: 0.3;
	  color: rgba(204, 224, 210, 0.2);
	}
	60% {
	  opacity: 0.6;
	  color: rgba(204, 224, 210, 0.3);
	}
	90% {
	  opacity: 0.8;
	  color: rgba(204, 224, 210, 0.6);
	}
	100% {
	  opacity: 1;
	  color: rgba(204, 224, 210, 0.9);
	}
}

@keyframes openModal {
	0% {
	  opacity: 0;
	  transform: translateY(-100%);
	}
	50% {
	  opacity: 1;
	  transform: translateY(20%);
	}
	100% {
	  opacity: 1;
	  transform: translateY(0%);
	}
}

@keyframes reassemble {
	100% {
		top: unset;
		left: unset;
	}
}