.portal-page {
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;
	padding: 0;
	background: linear-gradient(rgb(54, 0, 0), rgb(12, 0, 0));
}

.portal-home {
	display: flex;
	width: 100%;
	height: 100%;
	margin: 1vmin auto;
	transform: skewX(50deg);
}


.portal {
	display: flex;
	justify-content: center;
}

.peek-a-boo {
	position: relative;
	top: 300px;
	width: 70%;
	height: 70%;
	color: hsl(158, 16%, 64%);
}
